<template>
  <div>
    <van-row class="top_header" type="flex" justify="center">
      <van-row style="text-align: center;">
        <van-image round width="80" height="80" src="https://img01.yzcdn.cn/vant/cat.jpeg" />
      </van-row>
      <van-row class="text-name">
        {{name}}
      </van-row>
      <van-row class="text-name font-12">
        普通等级
      </van-row>
    </van-row>
    <van-cell class="cell-list" title="我的订单" icon="todo-list-o"  size="40"   to="/orderList" is-link />
    <van-grid :column-num="5">
      <van-grid-item icon="paid" text="待付款" @click="navTo('orderList',1)"/>
      <van-grid-item icon="exchange" text="待发货" @click="navTo('orderList',2)"/>
      <van-grid-item icon="logistics" text="待收货" @click="navTo('orderList',3)"/>
      <van-grid-item icon="records" text="待评价" @click="navTo('orderList',4)"/>
      <van-grid-item icon="replay" text="退换货" />
    </van-grid>


    <van-cell class="cell-list"  title="我的服务" icon="like-o" is-link />
    <van-grid :column-num="4">
      <van-grid-item icon="cluster-o" text="升级会员" />
      <van-grid-item icon="coupon-o" text="我的优惠券" />
      <van-grid-item icon="after-sale" text="我的余额" to="/balance"/>
      <van-grid-item icon="bill-o" text="我的发票" />

      <van-grid-item icon="points" text="我的积分" />
      <van-grid-item icon="location-o" text="地址管理" @click="addressManagement"/>
      <van-grid-item icon="star-o" text="我的收藏" />
      <van-grid-item icon="flower-o" text="我的足迹" />
    </van-grid>


    <van-cell class="cell-list"  title="其他" icon="home-o" is-link />
    <van-grid :column-num="4">
      <van-grid-item icon="friends-o" text="邀请好友" />
      <van-grid-item icon="setting-o" text="系统设置" to="/setting"/>
      <van-grid-item icon="phone-circle-o" text="联系客服" />
    </van-grid>

    <van-row style="display: none;">
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell title="我的订单" icon="home-o" is-link />
      <van-cell>
        <van-button square style="height: 2.5rem; " color="linear-gradient(to right, #ff6034, #ee0a24)"
          native-type="submit" size="large">退出登录</van-button>

      </van-cell>
    </van-row>





  </div>
</template>

<script>
  export default {
    data() {
      return {
        name:'12321312',
      };
    },
    methods: {
      addressManagement(){
        this.$router.push('address_list')
      },
      navTo(name, id) {
        this.$router.push({
          name: name,
          params: {
            id: id
          }
        })
      }
    }
  }
</script>

<style>
  .cell-list .van-cell__left-icon{
    color: #FF0000;
    font-size: 24px;
  }
  .top_header {
    background: url('http://zhongjun.ueoa.net/wap/static/image/member-bg.png');
    background-size: 100%;
    height: 170px;
    width: 100%;
    flex-direction: column;
  }
  .text-name{
    text-align: center;
    color: #FFF;
    font-size: 14px;
    margin-bottom: 5px;
  }
  .font-12{
    font-size: 12px !important;
  }


</style>



